ivas. Esta forma de comunicación favorece relaciones laborales equilibradas y constructivas, incluso en situaciones de desacuerdo o conflicto y contribuye al fortalecimiento de entornos de trabajo más colaborativos y saludables.")]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-12"},[_c('figure',[_c('div',{staticClass:"video"},[_c('iframe',{attrs:{"width":"560","height":"315","src":"https://www.youtube.com/embed/vdPrCjWJSHo?si=X4NxENRo3LLXAua_","title":"YouTube video player","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}})])]),_c('figcaption',[_vm._v("Video. ")])])])])
